<template>
  <v-dialog v-model="wordDialog" persistent>
    <v-img :src="topBoard">
      <v-container fluid fill-height class="pb-0 mb-0">
        <v-layout class="text-xs-center" align-end justify-center>
          <v-avatar size="64" color="#00c9db">
            <span :class="currentColor">{{ count }}</span>
          </v-avatar>
        </v-layout>
      </v-container>
    </v-img>
    <v-responsive>
      <v-img :src="require('@/assets/minimg/board_middle.png')">
        <v-container fluid fill-height>
          <v-layout align-center justify-center column mx-5>
            <h1>{{ word }}</h1>
            <v-chip @click="onselected(1)" :color="getColor(!d1)" text-color="white">{{ans1}}</v-chip>
            <v-chip @click="onselected(2)" :color="getColor(!d2)" text-color="white">{{ans2}}</v-chip>
            <v-chip @click="onselected(3)" :color="getColor(!d3)" text-color="white">{{ans3}}</v-chip>
            <v-chip @click="onselected(4)" :color="getColor(!d4)" text-color="white">{{ans4}}</v-chip>
          </v-layout>
        </v-container>
      </v-img>
    </v-responsive>
    <v-img :src="require('@/assets/minimg/board_bottom.png')">
      <v-container fluid fill-height>
        <v-layout align-center justify-end mt-4 mr-4>
          <v-flex xs5 lg5>
            <v-img
              v-if="itemCount > 0 && !athenaUsed"
              :src="require('@/assets/minimg/athena_c_on.png')"
              @click="useAthena"
            >
              <v-container fluid>
                <v-layout align-center justify-end>
                  <v-avatar color="red" size="32" ml-10 mt-3>
                    <span class="white--text headline">{{ itemCount }}</span>
                  </v-avatar>
                </v-layout>
              </v-container>
            </v-img>
            <v-img v-else :src="require('@/assets/minimg/athena_c_off.png')">
              <v-container fluid>
                <v-layout align-center justify-end>
                  <v-avatar color="grey" size="32" ml-10>
                    <span class="white--text headline">{{ itemCount }}</span>
                  </v-avatar>
                </v-layout>
              </v-container>
            </v-img>
          </v-flex>
        </v-layout>
      </v-container>
    </v-img>
  </v-dialog>
</template>

<script>
export default {
  data() {
    return {
      flag: true,
      topBoard: require("@/assets/minimg/board_top.png"),
      answer: 0,
      currentColor: "",
      count: 6,
      polling: null,
      previdx: 0,
      athenaUsed: false,
      d1: false,
      d2: false,
      d3: false,
      d4: false,
      itemCount: 0
    };
  },
  props: ["wordDialog", "qidx", "word", "ans1", "ans2", "ans3", "ans4", "answ"],
  methods: {
    timer: function() {
      this.count--;
    },
    countdown: function() {
      // setInterval이 병행 호출되어 타이머가 너무 빨라지는 것을 막기 위해 직전에 clearInterval을 호출한다.
      this.count = 6;
      this.currentColor = "black--text display-2";
      clearInterval(this.polling);
      this.polling = setInterval(this.timer, 1000);
    },
    onselected: function(ans) {
      clearInterval(this.polling);
      if (ans == this.answ) {
        if (this.flag) {
          this.flag = false;
          this.playbeep(require("@/assets/correct.wav"));
          this.topBoard = require("@/assets/minimg/board_top_o.png");
          setTimeout(() => {
            this.$emit("update:wordDialog", false);
            this.$root.$emit("answered", { index: this.qidx, correct: true });
            this.flag = true;
          }, 1500);
        }
      } else {
        if (this.flag) {
          this.flag = false;
          this.playbeep(require("@/assets/incorrect.wav"));
          this.topBoard = require("@/assets/minimg/board_top_x.png");
          setTimeout(() => {
            this.$emit("update:wordDialog", false);
            this.$root.$emit("answered", { index: this.qidx, correct: false });
            this.flag = true;
          }, 1500);
        }
      }
    },
    useAthena() {
      this.$store.commit("setAthena", -1);
      this.itemCount--;
      this.athenaUsed = true;
      let count = 0;
      if (count < 2 && 1 !== this.answ) {
        this.d1 = true;
        count++;
      }
      if (count < 2 && 2 !== this.answ) {
        this.d2 = true;
        count++;
      }
      if (count < 2 && 3 !== this.answ) {
        this.d3 = true;
        count++;
      }
      if (count < 2 && 4 !== this.answ) {
        this.d4 = true;
        count++;
      }
      this.countdown();
    },
    getColor(val) {
      if (val) return "indigo";
      else return "grey";
    }
  },
  computed: {},
  watch: {
    count: function() {
      if (this.count <= 3) {
        switch (this.count) {
          case 3:
            this.currentColor = "red--text display-2";
            break;
          case 0:
            this.onselected(0);
            break;
          default:
            break;
        }
        if (this.count > 0) this.playbeep(require("@/assets/countdown.mp3"));
      }
    }
  },
  // created, mounted는 팝업이 뜨기 전에 이미 호출이 된다.
  created() {
    //console.log('created....')
  },
  mounted() {
    //console.log('mounted....')
    this.itemCount = this.$store.getters.athena;
    this.currentColor = "black--text display-2";
    if (this.itemCount > 0) {
      this.athenaIcon = require("@/assets/minimg/athena_c_on.png");
    } else {
      this.athenaIcon = require("@/assets/minimg/athena_c_off.png");
    }
  },
  updated() {
    //console.log('updated....', this.qidx)
    // updated()는 팝업이 뜰때 한번 호출되고, 팝업이 닫힐때 또 한번 호출되기 때문에
    // 타이머가 중복 호출되지 않도록 아래 체크 로직을 사용해야 한다.
    if (this.previdx == 0 || this.previdx != this.qidx) {
      this.previdx = this.qidx;

      this.answer = 0;
      this.currentColor = "black--text display-2";
      this.count = 6;
      // 0인 경우부터 시작하면 팝업 이전에 메인화면이 뜰때부터 countdown이 한번 호출되는 문제가 있다.
      this.countdown();
    }
  }
};
</script>

